}

footer {
    color: black;
    font-size: 20px;
    text-align: center;
    
}

.social-media-icon {
    display: inline-block;
    list-style: none;
    padding: 5px;
}

i {
    font-size: 30px;
    text-align: center;
}

i:hover {
    color: purple;
    transition: .7s;
    transform: rotate(360deg);
}

.jumbotron {
    background-image: url("https://www.artsatl.org/wp-content/uploads/635873821517718970351055939_Stack-of-books-great-education-759x500.jpg");
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

p {
    font-size: 20px;
    text-align: center;
    color: white;
    font-weight: bolder;

}

#landingPageText {
    font-family: 'Kaushan Script', cursive;

}

.bg {
    background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("https://external-preview.redd.it/yZc0bxK3qvWcx1bfWxn-Sahbrw346a8GjeRgx2CDDWk.jpg?auto=webp&s=e65d09485c4c62c8b8a914e06dbf4ddb4a12106a");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    position: relative;
    height: 1000px;
}

.hero-text {
    text-align: center;
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
  }



